import React, {useState} from "react"
import Filmer from "../files/filmer"
import AlleFilmerSubModule from "./AlleFilmerSubModule"
import Stjerne from "../files/stjerne.png"
import { MdNavigateNext } from "react-icons/md"
import { MdNavigateBefore } from "react-icons/md"
import { MdFavorite } from "react-icons/md"
import "./TopRated.css"
import { IoIosSearch } from "react-icons/io"


const TopRated = () => {
    const [filmer,setFilmer] = useState(Filmer.movies.filter( f => f.rating >= 8 ))
    const [display, setDisplay] = useState(false)
    const [klikketPaa, setKlikketPaa] = useState(false)
    const itemsPerPage = 8
    const [pager, setPager] = useState({
        page: 1,
        start: 0,
        end: itemsPerPage
    }) 

    const updatePagerUp = () => {
        setPager({
            page: pager.page + 1,
            start: pager.start + itemsPerPage,
            end: pager.end + itemsPerPage    
        })
    }

    const updatePagerDown = () => {
        setPager({
            page: pager.page - 1,
            start: pager.start - itemsPerPage,
            end: pager.end - itemsPerPage    
        })
    }

    const filterMovies = (e) => {
        setPager({
            page: 1,
            start: 0,
            end: itemsPerPage    
        })

        setFilmer(
            Filmer.movies.filter( film => film.title.toLowerCase().includes((e.target.value).toLowerCase())||  
            film.director.toLowerCase().includes((e.target.value).toLowerCase()) || 
            film.actors.toString().toLowerCase().includes((e.target.value).toLowerCase()))
        )
    }

    const displayMe = () => {
        setDisplay(!display)
        setKlikketPaa(!klikketPaa)
    }

    return(
        <>
            <h1 className="top-rated">Top Rated</h1>
            <IoIosSearch className="search" onClick={!klikketPaa ? displayMe : ""}/>
            <input className={display ? "inpSearch" : "searchHide"} type="text" placeholder="Search for movies, actors, directors" onInput={filterMovies} />
            <div className="filmoversikt">
                {
                    filmer.slice(pager.start, pager.end).map(
                        film => <AlleFilmerSubModule 
                        title={film.title}
                        year={film.year}
                        director={film.director}
                        rating={film.rating}
                        poster={film.poster}
                        storyline={film.storyline}
                        actors={film.actors}
                        genres={film.genres}
                        stjerne={Stjerne}
                        favoritt={<MdFavorite className="hjerte"/>}
                        div={<div className="sirkel"></div>}
                        />
                    )
                }
                <div className="nav-div"><MdNavigateBefore className="nav-ikon" onClick={pager.page >=2 ? updatePagerDown :""}/> <p>Side {pager.page}</p> <MdNavigateNext className="nav-ikon" onClick={pager.end <filmer.length ? updatePagerUp : ""}/></div>
            </div>
    </>
    )
}

export default TopRated