import React, {useState} from "react"
import "./AlleFilmerSubModule.css"
import Filmer from "../files/filmer"
import MineFavoritter from "./MineFavoritter";

const AlleFilmerSubModule = (props) => {
    const [filmer,setFilmer] = useState(Filmer.movies)
    const [favourites,setFavourites] = useState([])
    
    const leggTilFavoritt = () => {
        const finn= favourites.find(favoritt => favoritt===filmer[props.indeks])
        if(finn){
            return;
        }
        setFavourites([...favourites,filmer[props.indeks]])
        console.log(favourites)
    }

    return(
        <div className="filmdiv">
            <div className="left">
                <img src={props.poster}/>
            </div>
            
            <div className="middle">
                {props.slett}
                <img className="stjerne" src={props.stjerne}/>
                <h4 className="rating">{props.rating}<span className="avTi"> / 10</span></h4>
                <div className="bottom">
                    <p className="storyline">{props.storyline}</p>
                </div>
                <div className="actors">
                    <h4 className="stars">Starring: 
                        {props.actors.map(actor =><span className="propsSpan">{actor}</span>)}
                    </h4>
                    <h4 className="stars">Director: <span className="propsSpan">{props.director}</span> </h4>
                    <div className="genre">
                        {props.genres.map(genre =><span>{genre} | </span>)}
                    </div>

                </div>
            </div>
            <div className="right">
                <div>
                    <h3>{props.title}</h3>
                    <h4 className="year">{props.year}</h4>
                </div>
                <div className="sirkel-div">
                    {props.div}
                    {props.favoritt}
                </div>
            </div>
        </div>
    )
}

export default AlleFilmerSubModule