import React,{useState} from "react"
import Filmer from "../files/filmer"
import AlleFilmerSubModule from "./AlleFilmerSubModule"
import Stjerne from "../files/stjerne.png"
import {TiDelete} from "react-icons/ti"
import { MdNavigateBefore } from "react-icons/md"
import { MdNavigateNext } from "react-icons/md"
import "./MineFavoritter.css"
import { tsPropertySignature } from "@babel/types";

const MineFavoritter = (props) => {

    console.log(props)

    const [filmer,setFilmer] = useState(Filmer.movies)
    const [favourites,setFavourites]=useState([])

    const leggTil=()=> {
        const finn=favourites.includes(filmer.id)
        if(finn){
            return;
        }
        setFavourites([...favourites,filmer[filmer.id]])
    }

    const remove=(i)=>{
        const kopi = [...favourites]
        kopi.splice(i, 1)
        setFavourites(kopi)
    }

    const itemsPerPage = 8
    const [pager, setPager] = useState({
        page: 1,
        start: 0,
        end: itemsPerPage
    }) 

    const updatePagerUp = () => {
        setPager({
            page: pager.page + 1,
            start: pager.start + itemsPerPage,
            end: pager.end + itemsPerPage    
        })
    }

    const updatePagerDown = () => {
        setPager({
            page: pager.page - 1,
            start: pager.start - itemsPerPage,
            end: pager.end - itemsPerPage    
        })
    }
    const filterMovies = (e) => {
        setPager({
            page: 1,
            start: 0,
            end: itemsPerPage    
        })

        setFilmer(
            Filmer.movies.filter( film => film.title.toLowerCase().includes((e.target.value).toLowerCase())||  
            film.director.toLowerCase().includes((e.target.value).toLowerCase()) || 
            film.actors.toString().toLowerCase().includes((e.target.value).toLowerCase()))
        )
    }
    return(
    <>
        <h1 className="h1favourites">Yor favourites</h1>
        
        <div className="filmoversikt">
            <input className="inpSearch" type="text" placeholder="Search for movies, actors, directors" onInput={filterMovies} />
                {
                    favourites.slice(pager.start, pager.end).map(
                        (favourite,i) => <AlleFilmerSubModule
                        leggTil={leggTil}
                        id={favourite.id}
                        myNumber={i}
                        title={favourite.title}
                        year={favourite.year}
                        director={favourite.director}
                        rating={favourite.rating}
                        poster={favourite.poster}
                        storyline={favourite.storyline}
                        stjerne={Stjerne}
                        genres={favourite.genres}
                        actors={favourite.actors}
                        slett={<TiDelete onClick={()=>remove(i)} className="slett"/>}
                    />
                    )
                }
            <div className="nav-div"><MdNavigateBefore className="nav-ikon" onClick={pager.page >=2 ? updatePagerDown :""}/> <p>Side {pager.page}</p> <MdNavigateNext className="nav-ikon" onClick={pager.end <filmer.length ? updatePagerUp : ""}/></div>

        </div>
    </>
    )
}


export default MineFavoritter